import { createSlice, PayloadAction, Slice } from '@reduxjs/toolkit'
import { SliceCaseReducers } from '@reduxjs/toolkit/src/createSlice'

export interface Confirmation {
    confirmed: boolean
}

const initialState: Confirmation = {
    confirmed: false,
}

const confirmationSlice: Slice<Confirmation> = createSlice<Confirmation, SliceCaseReducers<Confirmation>, 'confirmation'>({
    name: 'confirmation',
    initialState,
    reducers: {
        confirm(state: Confirmation, action: PayloadAction<string>) {
            state.confirmed = true
        },
    },
});

export { confirmationSlice }
export const { confirm } = confirmationSlice.actions
